"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import { getAllPosts } from "@/app/api/postApi";

export default function HomeFeedPage() {
  const [posts, setPosts] = useState([]);
  const [error, setError] = useState("");

  // Check auth
  useEffect(() => {
    const storedToken = localStorage.getItem("accessToken");

    if (!storedToken) {
      window.location.href = "/login";
      return;
    }

    async function loadFeed() {
      try {
        const response = await getAllPosts(storedToken);
        setPosts(response.posts || response);
      } catch (err: any) {
        setError(err.message);
      }
    }

    loadFeed();
  }, []);

  return (
    <div className="min-h-screen bg-gray-100 font-[Inter] p-6">
      {/* Header */}
      <header className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold">Pehnava Feed</h1>

        <button
          onClick={() => {
            localStorage.clear();
            window.location.href = "/";
          }}
          className="px-4 py-2 bg-gray-800 text-white rounded-lg"
        >
          Logout
        </button>
      </header>

      {error && (
        <p className="text-red-600 mb-4 text-center font-medium">{error}</p>
      )}

      {/* Loading */}
      {!error && posts.length === 0 && (
        <p className="text-center text-gray-600">Loading posts...</p>
      )}

      {/* Feed Grid */}
      <div className="grid gap-6 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3">
        {posts.map((p: any) => (
          <div
            key={p.postId}
            className="bg-white rounded-xl shadow-md overflow-hidden hover:shadow-xl transition-all"
          >
            {/* Image */}
            <div className="relative h-56 w-full">
              <Image
                src={`https://pehenava-backend.vercel.app/${p.photo}`}
                alt="Post Image"
                fill
                className="object-cover"
              />
            </div>

            {/* Content */}
            <div className="p-4">
              {/* Creator */}
              <div className="flex items-center gap-3 mb-3">
                <div className="w-11 h-11 bg-gray-200 rounded-full flex items-center justify-center text-xl font-bold">
                  {p.creator?.Name?.charAt(0)}
                </div>

                <div>
                  <p className="font-semibold">{p.creator?.Name}</p>
                  <p className="text-sm text-gray-500">{p.creator?.role}</p>
                </div>
              </div>

              <h2 className="text-lg font-bold">{p.postName}</h2>

              <p className="text-sm text-gray-600 mt-1 line-clamp-3">
                {p.description}
              </p>

              {/* Likes + Comments */}
              <div className="flex items-center justify-between mt-4 text-sm text-gray-700">
                <span>‚ù§Ô∏è {p.likesCount} Likes</span>
                <span>üí¨ {p.feedbacks?.length || 0} Feedbacks</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
